import React from "react";
import './NavBar.css';
import Logo from './media/artech.png';
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
const NavBar = () => {

    const [isClicked, setClicked] = useState(false);

    function clicked() {
        setClicked(!isClicked)

    }
    useEffect(() => {
        const navlinks = document.getElementsByClassName("menu-link")
        if (window.location.pathname == '/' && window.innerWidth > 768) {



            navlinks[0].style.borderBottom = '3px red solid'


        }
        else if (window.location.pathname == '/services' && window.innerWidth > 768) {
            navlinks[1].style.borderBottom = '3px red solid'

        }
        else if (window.location.pathname == '/solutions' && window.innerWidth > 768) {
            navlinks[2].style.borderBottom = '3px red solid'

        }
        else if (window.location.pathname == '/contact' && window.innerWidth > 768) {
            navlinks[3].style.borderBottom = '3px red solid'

        }
        function scrolling() {
            if (window.location.pathname == '/' && window.innerWidth > 768) {
                if (window.scrollY > 700)
                    Array.from(navlinks).forEach(element => {
                        element.style.color = 'black'
                        console.log("hello")
                    });
                if (window.scrollY < 700)
                    Array.from(navlinks).forEach(element => {
                        element.style.color = 'white'
                        console.log("hello")
                    });
                if (window.innerWidth >= 1024 && window.pathname == '/') {
                    if (window.scrollY >= 1715 && window.scrollY < 3215) {

                        navlinks[2].style.borderBottom = '3px red solid'


                    }
                }


            }
            if (window.location.pathname == '/services' && window.innerWidth > 768) {
                if (window.scrollY > 550)
                    Array.from(navlinks).forEach(element => {
                        element.style.color = 'black'
                        console.log("hello")
                    });
                if (window.scrollY < 550)
                    Array.from(navlinks).forEach(element => {
                        element.style.color = 'white'
                        console.log("hello")
                    });
            }
            if (window.location.pathname == '/solutions' && window.innerWidth > 768) {
                if (window.scrollY > 600)
                    Array.from(navlinks).forEach(element => {
                        element.style.color = 'black'
                        console.log("hello")
                    });
                if (window.scrollY < 600)
                    Array.from(navlinks).forEach(element => {
                        element.style.color = 'white'
                        console.log("hello")
                    });
            }
        }
        window.addEventListener('scroll', scrolling)

        return () => window.removeEventListener('scroll', scrolling)

    }, [window.scrollY])

    return (
        <nav>
           <Link to='/'> <div className="logo">
                <img src={Logo} />
            </div>
            </Link>
            <div className="bars" onClick={clicked}>
                <span className={isClicked ? 'bar active' : 'bar'}></span>
                <span className={isClicked ? 'bar active' : 'bar'}></span>
                <span className={isClicked ? 'bar active' : 'bar'}></span>
            </div>
            <ul className={isClicked ? 'menu' : 'menu hidden'}>
                <li className="menu-item">
                    <Link to='/' className="menu-link">Home</Link>
                </li>
                <li className="menu-item">
                    <Link to='/services' className="menu-link">Services</Link>
                </li>
                <li className="menu-item">
                    <Link to='/solutions' className="menu-link">Solutions</Link>
                </li>
                <li className="menu-item">
                    <Link to='/contact' className="menu-link">Contact</Link>
                </li>
            </ul>
        </nav>
    )

}
export default NavBar