import './Service.css'
import NavBar from './NavBar';
import Footer from './Footer';
import { useEffect } from 'react';
const Service = () => {


    useEffect(() => {
        const deployToggle = document.querySelector('.deploy_toggle')
        const IT_toggle = document.querySelector('.IT_toggle')
        const ITtext = document.querySelector('.It_text')
        const deployText = document.querySelector('.Deploy_text')
        var bool = true;
        deployToggle.addEventListener('click', () => {
            deployToggle.classList.add('active')
            ITtext.style.display = 'none';
            IT_toggle.classList.remove('active')
            deployText.style.display = 'block'
        })
        IT_toggle.addEventListener('click', () => {
            IT_toggle.classList.add('active')
            ITtext.style.display = 'block';
            deployToggle.classList.remove('active')
            deployText.style.display = 'none'
        })

    }, [])

    return (
        <>
            <NavBar />
            <div className='hero_services'>
                <div className='hero_text'>


                    <h2>Services</h2>
                    <h1>At Artech 2000 all services are tailored to your specific IT needs</h1>
                    <p> We start by identifying your needs. Then we like to build a full and acute understanding about a client and their company. This in turn allows us to guarantee that the most appropriate solutions are chosen.
                    </p>
                </div>
            </div>
            <section>
                <div className='service_container'>
                    <div className='service_toggle'>
                        <h2 className='IT_toggle active'>IT Consultancy</h2>
                        <h2 className='deploy_toggle'>Deployment</h2>
                    </div>
                    <p className='It_text'>
                        At Artech 2000 we understand that your business needs are unique; which is why we certify that all services are tailored to your specific IT needs.

                        <br /><br />  First and foremost we start by identifying your needs, these can be issues which are currently impacting the business or can be future strategies which the business intends to implement.

                        <br /><br /> We build a full and acute understanding about our clients and their companies.

                        <br /><br />  This in turn allows us to guarantee that the most appropriate solutions are chosen.

                        <br /><br /> To discuss your requirements contact us on <span className='bolding'>08001070120</span> or email us at <span className='bolding'>itenquiries@artech-2000.com</span>
                    </p>
                    <p className='Deploy_text'>
                        At Artech 2000 we pride ourselves with effective deployment and management of critical systems.

                        <br /><br />   We offer customised deployment solutions.

                        <br /><br />           To discuss your requirements, please call us on <span className='bolding'>08001070120</span> or email us at <span className='bolding'>itenquiries@artech-2000.com</span>
                    </p>
                </div>
            </section>
            <Footer />
        </>
    )
}
export default Service;